@import url(https://fonts.googleapis.com/css?family=Manjari:400,700&display=swap);
.navbar-app{font-size:1.5rem;background-color:#c5d7de;padding-left:10px;display:flex;align-items:center}

.navbar-board{font-size:1.1rem;background-color:#5b858f;padding-left:10px;display:flex;align-items:center}

.main-board{background-color:#d9d9d9;display:flex;min-height:100vh}

.board-columns{display:flex;overflow-x:auto}

.column{flex:0 0 auto;width:300px;height:calc(100% - 10px);margin-left:10px;margin-top:10px}.column>*{color:#333;padding:5px 8px}.column .list-task{margin-top:5px;max-height:75vh;overflow:auto}.column .list-task::-webkit-scrollbar{width:10px}.column .list-task::-webkit-scrollbar-track{box-shadow:inset 0 0 5px grey;border-radius:10px}.column .list-task::-webkit-scrollbar-thumb{background:whitesmoke;border-radius:10px}.column .list-task::-webkit-scrollbar-thumb:hover{background:#b30000}.column header{padding-left:10px;height:36px;line-height:36px;line-height:36px;font-size:16px;font-weight:bold;border-top-left-radius:5px;border-top-right-radius:5px;cursor:pointer}.column header .column-drag-handle{display:flex}.column .date_column{font-size:10px;font-weight:bold;margin-left:10px;margin-top:3px}.column footer{padding-left:10px;height:36px;line-height:36px;border-bottom-left-radius:5px;border-bottom-right-radius:5px;color:#888}.column footer .footer-action{margin-left:5px;cursor:pointer}.column footer .footer-action:hover{color:#333}.column .icon{font-size:14px;margin-right:5px}.column .add-new-task{background-color:whitesmoke;border-radius:3px;width:285px;margin-left:7px;box-shadow:0 1px 1px #000}.column .add-new-task .group-btn{display:flex;align-items:center;padding-top:5px}.column .add-new-task .group-btn .btn.btn-success{font-size:10px;font-weight:bold;width:60px;height:30px;padding:5px 5px 5px 5px;background-color:green;color:black}.column .add-new-task .group-btn .fa.fa-times{font-size:25px;padding-left:10px;margin-top:12px}.column .add-new-task .form-control{width:270px;padding-left:5px;border-radius:3px}.column .task-drop-preview{background-color:rgba(150,150,200,0.1);border:1px dashed #abc;margin:5px 5px 5px 5px}

.task-item{background-color:white;padding:10px;border-radius:3px;box-shadow:0 1px 1px #000;margin-bottom:10px;font-weight:bold}.task-item:not(:last-child){margin-bottom:10px}.task-item .decription-task{font-size:12px;font-weight:normal;color:#5b5757}.task-item .checker{font-size:100px}.task-item .hashtag-of-task{text-align:right;font-size:10px}.task-item .header-task{display:flex;justify-content:space-between}.task-item .header-task .title-task{max-width:200px}.task-item .header-task .title-task:hover{cursor:pointer;font-style:italic}.task-item .header-task .info-task{text-align:right;color:gray}.task-item .header-task .info-task:hover{color:#333;cursor:pointer}

.subtask-item{padding-left:10px;font-size:12px;font-weight:normal;color:#5b5757;margin-top:2px}.subtask-item:not(:last-child){margin-bottom:2px}

body{background:rgba(0,0,0,0.6);min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content;width:100vw;height:100vh;top:0;left:0;right:0;bottom:0;position:fixed}.body-form{text-align:center;background-color:white;width:300px;margin-left:calc(50% - 150px);margin-right:calc(50% - 150px);margin-top:50px;padding:10px 10px 10px 10px;border-radius:3px;box-shadow:0 1px 1px #000}.body-form .input-title{display:flex;padding-top:10px;padding-bottom:10px;justify-content:space-between}.body-form .input-description{display:flex;text-align:left;justify-content:space-between}.body-form .list-hashtag{font-size:16px;text-align:left}.body-form .list-hashtag .basic-multi-select{margin-top:20px;width:280px;text-align:right}.body-form .board-subtask{font-weight:normal;text-align:left;margin:10px 10px 0 0}.body-form .board-subtask .list-subtask{margin-left:20px}.body-form .board-subtask .name-subtask{font-weight:normal;font-size:15px}.body-form .date-task{margin-top:10px;margin-bottom:10px;display:flex;justify-content:space-between}.body-form .date-task .name-date{width:120px;text-align:left}.body-form .date-task .date{margin-left:60px}.body-form .date-task .add-action{font-weight:normal}.body-form .due-date{margin-top:10px;margin:bottom 10px;display:flex;justify-content:space-between}.body-form .due-date .name-duedate{width:100px;text-align:left}.body-form .due-date .duedate{margin-left:70px}.body-form .add-new-subtask{display:flex;justify-content:space-between;margin-top:10px;font-weight:normal;font-size:14px}.body-form .buttons{justify-content:space-between;padding-top:20px}.body-form .buttons .save-button{color:gray;margin-right:50px}.body-form .buttons .cancel-button .return-dashboard{text-decoration:none}.body-form .buttons .delete-button{margin-left:50px}

.subtask-item{display:flex;justify-content:space-between}.subtask-item .subtask{text-align:left;background-color:inherit;border:none}.subtask-item .subtask:hover{color:#252424;cursor:pointer}.subtask-item:focus{outline:none !important;box-shadow:none !important}.subtask-item .edit-button{width:30%;color:#646161;text-align:right}.subtask-item .edit-button .edit-title-button{padding-right:5px}.subtask-item .edit-button .edit-title-button:hover{color:#252424;cursor:pointer}.subtask-item .edit-button .delete-button:hover{color:#252424;cursor:pointer}

body{font-family:Manjari, sans-serif;font-size:14px}body .filter-column{display:block}body .filter-column .calendar{width:250px;height:280px;border-radius:3px}body .list-hashtag{margin-top:5px;height:calc(100% - 280px - 12px);background-color:white;border-radius:3px;padding:8px 5px 0 10px;font-weight:bold}body .list-hashtag .hashtag-item{display:flex;align-items:flex-end;padding:10px 5px 0 10px;max-width:220px;overflow-x:hidden;overflow-y:hidden;color:black;text-align:left;font-size:13px;font-weight:normal;justify-content:space-between}body .list-hashtag .add-new-hashtag{padding-top:20px;color:gray;font-weight:bold}body .list-hashtag .add-new-hashtag .link-form-add-hashtag{text-decoration:none;color:inherit}body .list-hashtag .add-new-hashtag:hover{color:#333;cursor:pointer}

.hashtags-list-item ul{list-style-type:none;text-align:left}li:hover{background-color:#4dcd7d;-webkit-text-decoration:under-line;text-decoration:under-line;transition:ease-out 0.7s}.hashtags-list-item li{margin-left:-35px}.list-group-item p{display:inline-block;margin-top:7px;font-weight:500}.hashtags div{display:block;width:100%}.create-channel text{border:none;border-color:transparent}.drop-menu-hashtag div{position:absolute;left:210px;margin-top:-15px;border:none}.drop-menu-hashtag button{border:none;background-color:#4dcd7d}.ml-auto{position:absolute;left:60%;margin-top:-15px;width:5px}.btn{width:20%;margin-top:5%;padding-bottom:20px !important}.list-group-item{border:none !important}.sub-hash-tag:hover .active{display:block}.sub-hash-tag .active{display:none}.popup div{width:350px}.popup_btn{border:none;width:20%;padding-top:10px;background-color:transparent}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:14px;line-height:1.3em}.sunsama-clone{min-height:100vh;display:grid;grid-template-rows:40px 50px 1fr;background-color:#d9d9d9}

